import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import ProductList from '../ProductList/ProductList';
import NavigationBtn from '../NavigationBtn/NavigationBtn';
import classes from './Category.module.css';

class Category extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
        this.props.setPrice(false);
    }
    
    render(){
        const index = this.props.match.params.id;
        const cat = this.props.categories[index];
        const products = this.props.getProducts(index);
        
        const takeAwayOption = this.props.takeAwayOption(index);
        console.log(takeAwayOption);

        let priceBtns = null;
        if(takeAwayOption){
            priceBtns = (
                <div className={classes.PriceBtns}>
                    <p>Εμφάνιση Τιμών : </p>
                    <button className={(!this.props.takeAway ? classes.ShopBtnActive : classes.ShopBtn)} 
                        onClick={() => this.props.setPrice(false)}>
                        {//this.props.takeAway ? "Απόκρυψη τιμών Take Away" : "Εμφάνιση τιμών Take Away"
                        }
                        Καταστήματος
                    </button>
                    <button className={(this.props.takeAway ? classes.TakeAwayBtnActive : classes.TakeAwayBtn)} 
                        onClick={() => this.props.setPrice(true)}>
                        {//this.props.takeAway ? "Απόκρυψη τιμών Take Away" : "Εμφάνιση τιμών Take Away"
                        }
                        Delivery
                    </button>
                </div>
            );
        }
        
        let topNavigation = (
            <div className={classes.TopNavigation}>
                <Link to='/Menu'>
                    <button><img src="assets/icon/arrow_back.svg" alt="main menu"/></button>   
                </Link>
            </div>
        );
        
        return (
            <div className={classes.Category}>
                <h2 >{cat.name[0]}</h2> 
                {topNavigation}
                {cat.hasOwnProperty('header') 
                    ?   <div className={classes.CategoryHeader}>
                            {cat.header}
                        </div> 
                    :   null}

                {priceBtns}

                <ProductList
                    takeAway = {this.props.takeAway} 
                    products={products} /> 

                {cat.hasOwnProperty('preFooter') 
                    ?   <div className={classes.CategoryPreFooter}>
                            {cat.preFooter}
                        </div> 
                    :   null}
                {cat.hasOwnProperty('footer') 
                    ?   <div className={classes.CategoryFooter}>
                            {cat.footer}
                        </div> 
                    :   null}
                

                <aside>
                    <p>Στις τιμές περιλαμβάνεται ΦΠΑ</p>
                    <p>Ο καταναλωτής δεν έχει υποχρέωση να πληρώσει εάν δεν
λάβει το Νόμιμο παραστατικό στοιχείο
(Απόδειξη ή Τιμολόγιο)</p>
                </aside>
                    
                <div className={classes.Back} >
                    <Link to='/Menu'>
                        Επιστροφή στο κεντρικό μενού
                    </Link>
                </div>  
            </div>
        )
   }
}

export default Category;
