import React from 'react';
import classes from './Footer.module.css';

const Footer = () => {
    return (
        <div className={classes.Footer}>
            <div className={classes.PhonesWrapper}>
                <h3>ΤΗΛΕΦΩΝΑ ΓΙΑ ΠΑΡΑΓΓΕΛΙΕΣ</h3>
                <div className={classes.Phones}>
                    <div><a href="tel:+302463023050" className="telephone">2463023050</a></div>
                    <div>
                        What's Up
                        <a href="tel:+306989832568" className="telephone">6989832568</a>
                    </div>
                    
                </div>
            </div>
            <div className={classes.FooterWrapper}>
                <div className={classes.SocialWrap}>
                    <div className={classes.Shop}>
                        <h3>PIZZA ΖΟΡΜΠΑΣ</h3>
                        <ul className={classes.Social}>
                            <li><a href="https://www.facebook.com/Pizzazorbas"><img src="./assets/icon/facebook.svg" alt="facebook logo"/></a></li>
                            <li><a href="https://www.instagram.com/pizzazorbas/"><img src="./assets/icon/instagram.svg" alt="instagram logo"/></a></li>
                        </ul>
                    </div>
                </div>
                <div className={classes.Address}> 
                    <address>Πάρκο Μελανοφρύδη <span>Πτολεμαΐδα</span></address>
                    
                </div>
                
                
                <div className={classes.Developer}>
                    <p>Web App Developed And Designed by</p>
                    <h6><a href="https://www.instagram.com/yanniotis/">Petros Gianniotis</a></h6>
                </div>    
            </div>          
        </div>
    )
}

export default Footer
