  componentDidMount() {
        const logo = document.querySelector('.intro .intro-logo');
        setTimeout(function(){
          logo.classList.add('show');
          const info = document.querySelector('.intro .enter-info');
          setTimeout(function(){
            info.classList.add('show');
            const enter = document.querySelector('.intro .enter-btn');
            setTimeout(function(){enter.classList.add('show')}, 1000)
          }, 1000);
        }, 900);
  }
  

  getProducts = (id) => {
    //console.log("get products" + index);
    let products = [];
    this.state.products.forEach((category, index) => {
      if(category.cat_id === parseInt(id)){
        let itemToPush = {...category, id : index};
        products.push(itemToPush);
      }
    });
    return products;
  }

  hasTakeAway = (id) => {
    return this.state.categories[id].takeAway;
  }

  setPrice = (flag) => {
    this.setState({takeAway : flag});
  }

  hideIntro = () => {
    const intro = document.querySelector('.intro');
    intro.classList.add('hide');  
  }


